/*Estilos para la web de conceptos para el desarrollo front-end*/

@import url('https://fonts.googleapis.com/css?family=Lato:400,700');
@import url('https://fonts.googleapis.com/css?family=Merriweather:700');
@import url('https://fonts.googleapis.com/css?family=Coiny:400');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    position: relative;
    min-height: 100%;
}

body {
    font: 400 16px/1.6 'Lato', sans-serif;
    color: #000078;
}
