.tituno {
    animation-duration: 4s;
    animation-name: slidein;
    text-align: center;
}

@keyframes slidein {
   from {
     margin-left: 100%;
     width: 300%; 
  }
 
   to {
     margin-right: 0%;
     width: 100%;
  }
}
