/*Maquetación común en todas las páginas*/
.migaspan {
	text-align: left;
}
.migaspan nav ul li {
   display: inline-block;
}
/*Estilo para el cuerpo de la página*/
/*Diseño responsive*/
.container {
    margin-right: auto;
    margin-left: auto;
    padding-right: 15px;
   padding-left: 15px;
    width: 100%;
	display: -webkit-flex; /* Safari copiado de la página https://www.w3schools.com/cssref/css3_pr_flex-wrap.asp*/
  -webkit-flex-wrap: wrap; /* Safari 6.1+ */
	display: flex;
	flex-wrap:wrap;
}
@media (min-width: 576px) {
    .container {
      max-width: 350px;
    }
}
  
@media (min-width: 768px) {
    .container {
      max-width: 550px;
    }
}
  
@media (min-width: 992px) {
    .container {
      max-width: 750px;
    }
}
  
@media (min-width: 1200px) {
    .container {
      max-width: 900px;
    }
}
